<template>
  <div class="page1">
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide v-for="item in carrousel" :key="item.id">
          <div class="carrsoule-img-box"><a v-bind:href="'#'+item.href"><img v-bind:src="item.src" alt=""></a></div>
      </swiper-slide>
      <div class="swiper-pagination" ref="pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
  name : "SmallCarrousel", 
  components: {
    swiper,
    swiperSlide
  },
  props:['page'],
  data() {
      return {
        swiperOption: {//swiperapihttp://www.swiper.com.cn/api/
          // notNextTicknotNextTicktrueNextTickswiperswiper<br>swipertrue
          notNextTick: true,
          // swiper configs swiperapi
          width: window.innerWidth,
          autoplay: true,
          direction : 'horizontal',
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          mousewheelControl : false,
          observeParents:true,
          pagination: {
            el: '.swiper-pagination',
            clickable:true
          },
          // debugger
          debugger: true,
          watchSlidesProgress : true,
          watchSlidesVisibility : true,
          loop:true,
          on:{
            // transitionEnd: function(swiper){
            //   // console.log(this.progress)
            // }
          },
        },
      }
  },
  computed:{
    carrousel(){
      let name = 'carrousel'+this.page
      return this.$store.state[name]
    }
  },mounted(){
    // console.log(this.page)
  }

}
</script>

<style>
.swiper-pagination-bullet-active{
  background-color: #FF6b00;
}
</style>



